




























































































































.item {
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  background-color: #2f3136;
  border-radius: 7px;
  margin-bottom: 15px;
  width: calc(25% - 15px);
  box-sizing: border-box;
  font-size: 0.85rem;
  text-align: center;
  &--folder {
    padding: 45px 15px 15px;
  }
  &--file {
  }
}
.file {
  &__thumb {
    max-width: 100%;
  }
  &__name {
    padding: 10px;
  }
}
